import React, { useState, cloneElement, forwardRef, useRef, useEffect } from 'react'
import Measure from 'react-measure'
import { saveAs } from 'file-saver'
import { SlMagnifierAdd, SlMagnifierRemove } from "react-icons/sl";

import Box from 'components/Box'
import bg from './RenderTable/bg.png'
import BackgroundImage from 'components/BackgroundImage'
import Dom2Canvas from 'utils/dom2canvas'
import Button from 'components/Button'
import Flex from 'components/Flex'
import Text from 'components/Text';
import { timeColors, timemark } from './PortfolioPage';
import NoteBox from './NoteBox';
import { last } from 'lodash';

const PageSimulator = forwardRef(({ children, noBg, width = 1920, height = 1080, name, downloading, downloadRef }, ref) => {
  const [isDownloading, setIsDownloading] = useState(false)
  const [scale, setScale] = useState(1)
  const [scaleOfMap, setMapScale] = useState(1)
  const wrapperRef = useRef()
  const handleDownload = () => new Promise((res) => {
    setIsDownloading(true)
    const date = new Date().toLocaleDateString()
    setTimeout(() => {
      const ins = new Dom2Canvas(wrapperRef.current, width, height)
      ins.toCanvas((canvas) => {
        let c = canvas
        if (canvas.width !== width) {
          const newCanvas = document.createElement('canvas')
          newCanvas.width = width
          newCanvas.height = height
          const ctx = newCanvas.getContext('2d')
          ctx.drawImage(canvas, 0, 0, width, height)
          c = newCanvas
        }

        c.toBlob(blob => {
          saveAs(blob, `${date}-${name}.png`);
          setIsDownloading(false)
          res()
        })
      })
    })
  })
  useEffect(() => {
    if (downloadRef) {
      downloadRef.current = handleDownload
    }
  }, [downloadRef])
  return (
    <>
      <Measure
        bounds
        onResize={contentRect => {
          setScale(contentRect.bounds.width / width)
        }}
      >
        {({ measureRef }) => <div ref={measureRef} />}
      </Measure>
      <Box pt={`${height / width * 100}%`} position="relative">
        <Box.FullAbsolute
          style={{
            transform: `scale(${scale})`,
            transformOrigin: '0 0'
          }}
          ref={ref}
        >
          <BackgroundImage
            position="relative"
            width={width}
            height={height}
            ref={wrapperRef}
            src={noBg ? ((isDownloading || downloading) ? null : bg) : bg}
            pointerEvents="none"
            userSelect={process.env.NODE_ENV === 'production' && 'none'}
            fontFamily="'Noto Sans', sans-serif"
          >
            {cloneElement(children, { transform: `scale(${scaleOfMap})`, transformOrigin: 'center' })}
          </BackgroundImage>
        </Box.FullAbsolute>
      </Box>
      <Flex alignItems="center" my="2em">
        <Button disabled={isDownloading} onClick={handleDownload}>下載</Button>
        <Button
          onClick={() => setMapScale(prev => prev - 0.05)}
          mx="0.5em"
        >
          <SlMagnifierRemove />
        </Button>
        <Button
          onClick={() => setMapScale(prev => prev + 0.05)}
        >
          <SlMagnifierAdd />
        </Button>
        <Text ml="0.5em">{Math.round(scaleOfMap * 100)}%</Text>
      </Flex>
    </>
  )
})

export default PageSimulator
